.Container {
    width: 100%;
    max-width: 1000px;
    padding: 50px 0px 30px;
    text-align: center;
    box-sizing: border-box;
    margin: auto;
}

.socialButtons {
    max-width: 280px;
    margin: auto;
    margin-top: 50px;
}

.socialButtons > button {
    margin-bottom: 10px !important;
}

.Background {
    background: url('https://websiter.s3.us-east-2.amazonaws.com/signin.jpg');
    width: 100%;
    height: 300px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
